import * as React from "react";

export type ButtonVariant = "primary" | "secondary" | "ghost" | "danger" | "warning" | "success";
export type ButtonSize = "xs" | "sm" | "md";

export function buttonClassName(options?: {
  variant?: ButtonVariant;
  size?: ButtonSize;
  fullWidth?: boolean;
  className?: string;
}): string {
  const variant: ButtonVariant = options?.variant ?? "secondary";
  const size: ButtonSize = options?.size ?? "sm";

  const base =
    "inline-flex select-none items-center justify-center gap-2 whitespace-nowrap rounded-lg transition focus:outline-none focus:ring-2 focus:ring-[var(--ring)] disabled:pointer-events-none disabled:opacity-60";

  const sizeClass =
    size === "xs"
      ? "h-8 px-3 text-[12px]"
      : size === "md"
        ? "h-11 px-5 text-sm"
        : "h-10 px-4 text-sm";

  const variantClass =
    variant === "primary"
      ? "bg-[var(--accent)] text-white font-semibold shadow-[var(--shadow)] hover:brightness-105"
      : variant === "ghost"
        ? "bg-transparent text-[var(--foreground)] font-semibold hover:bg-[var(--card-2)]/60"
        : variant === "danger"
          ? "bg-[var(--down)] text-white font-semibold shadow-[var(--shadow)] hover:brightness-105"
          : variant === "warning"
            ? "bg-[var(--warn)] text-white font-semibold shadow-[var(--shadow)] hover:brightness-105"
            : variant === "success"
              ? "bg-[var(--up)] text-white font-semibold shadow-[var(--shadow)] hover:brightness-105"
          : "border border-[var(--border)] bg-[var(--card)] text-[var(--foreground)] font-semibold shadow-[var(--shadow)] hover:bg-[var(--card-2)]";

  const widthClass = options?.fullWidth ? "w-full" : "";
  const extra = options?.className ?? "";

  return [base, sizeClass, variantClass, widthClass, extra].filter(Boolean).join(" ");
}

export type ButtonProps = Omit<React.ButtonHTMLAttributes<HTMLButtonElement>, "className"> & {
  variant?: ButtonVariant;
  size?: ButtonSize;
  fullWidth?: boolean;
  className?: string;
};

export const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(function Button(
  { variant, size, fullWidth, className, type, ...props },
  ref,
) {
  return (
    <button
      ref={ref}
      type={type ?? "button"}
      className={buttonClassName({ variant, size, fullWidth, className })}
      {...props}
    />
  );
});
